.stars {
  display: flex;

  &__item {
    background-image: url(../images/star.svg);
    margin-right: 4px;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center center;
  }

  &:last-child {
    margin-right: 0;
  }
}

@for $i from 0 through 5 {
  .stars__stars--#{$i}>:nth-child(-n + #{$i}) {
    background-image: url("../images/star-active.svg");
  }
}
